<template>
	<div class="business-scale-analysis">
		<box-item title="经营规模分析">
			<div class="echart-container" ref="EContainer"></div>
			<div class="legend">
				<div class="left">
					<p><span class="icon one"></span><span>农户（大户）：13755家</span></p>
					<p><span class="icon two"></span><span>家庭农场：2460家</span></p>
					<p><span class="icon three"></span><span>村集体：1908家</span></p>
				</div>
				<div class="right">
					<p><span class="icon four"></span><span>农业企业：13755家</span></p>
					<p><span class="icon five"></span><span>合作社：2460家</span></p>
					<p><span class="icon six"></span><span>其他：1908家</span></p>
				</div>
			</div>
		</box-item>
	</div>
</template>

<script>
import BoxItem from '@/components/common/BoxItem';
const pieOption = {
	color: ['#CB4A99', '#D5EF10', '#F6A82B', '#32CEBB', '#10AC2A', '#3649FB'],
	series: [
		{
			type: 'pie',
			center: ['50%', '50%'],
			radius: ['82%', '72%'],
			hoverAnimation: false,
			label: {
				show: false
			},
			labelLine: {
				show: false
			},
			data: [
				{value: 335, name: '农户（大户）'},
				{value: 310, name: '家庭农场'},
				{value: 234, name: '村集体'},
				{value: 135, name: '农业企业'},
				{value: 1548, name: '合作社'},
				{value: 158, name: '其他'}
			]
		},
		{
			type: 'pie',
			center: ['50%', '50%'],
			radius: ['66%', '64%'],
			hoverAnimation: false,
			label: {
				show: false
			},
			labelLine: {
				show: false
			},
			data: [335, 310, 234, 135, 1548, 158]
		}
	],
	graphic: [
		{
			type: 'text',
			left: 'center',
			top: '30%',
			style: {
				text: '经营主体',
				textAlign: 'center',
				fill: '#9DD1D7', // 文字的颜色
				fontSize: 16
			}
		},
		{
			type: 'text',
			left: 'center',
			top: '42%',
			style: {
				text: 77627,
				textAlign: 'center',
				fill: '#FEEB48', // 文字的颜色
				fontSize: 30
			}
		},
		{
			type: 'text',
			left: 'center',
			top: '60%',
			style: {
				text: '(家)',
				textAlign: 'center',
				fill: '#FEEB48', // 文字的颜色
				fontSize: 12
			}
		}
	]
};
export default {
	data() {
		return {
			chartInstance: null
		};
	},
	computed: {
		EContainer() {
			return this.$refs.EContainer;
		}
	},
	mounted() {
		this.$nextTick(() => {
			this.initMoudle();
		});
	},
	methods: {
		async initMoudle() {
			this.chartInstance = this._initEcharts(this.EContainer, pieOption);
		},
		_initEcharts(el, option) {
			const chart = this.$echarts.init(el);
			chart.setOption(option);
			return chart;
		},
		refreshRender() {
			if (this.chartInstance) {
				this.chartInstance.resize();
			}
		}
	},
	components: {
		BoxItem
	}
};
</script>

<style lang="scss" scoped>
.business-scale-analysis {
	height: 45%;
	box-sizing: border-box;
	margin-bottom: 15px;
	//padding: 25px 0 30px;
	// background-color: rgba(4, 41, 64, 0.6);
	.echart-container {
		width: 100%;
		height: 60%;
	}
	.legend {
		width: 100%;
		box-sizing: border-box;
		padding: 0 20px;
		display: flex;
		justify-content: space-between;
		margin-top: 20px;
		font-size: 14px;
		color: #9dd1d7;
		p {
			display: flex;
			align-items: center;
			margin-bottom: 6%;
			.icon {
				display: inline-block;
				width: 8px;
				height: 8px;
				border-radius: 50%;
				margin-right: 10px;
				&.one {
					background: #cb4a99;
				}
				&.two {
					background: #d5ef10;
				}
				&.three {
					background: #f6a82b;
				}
				&.four {
					background: #f6a82b;
				}
				&.five {
					background: #10ac2a;
				}
				&.six {
					background: #3649fb;
				}
			}
		}
	}
}
</style>



